<template>
    <div>
        <div class="page-titles">
            <div class="row">
                <div class="col-12 col-sm-6">
                    <h3 class="text-themecolor">{{trans('inventory.stock_transfer')}}
                        <span class="card-subtitle d-none d-sm-inline" v-if="stock_transfers.total">{{trans('general.total_result_found',{count : stock_transfers.total, from: stock_transfers.from, to: stock_transfers.to})}}</span>
                        <span class="card-subtitle d-none d-sm-inline" v-else>{{trans('general.no_result_found')}}</span>
                    </h3>
                </div>
                <div class="col-12 col-sm-6">
                    <div class="action-buttons pull-right">
                        <button class="btn btn-info btn-sm" v-if="stock_transfers.total && !showCreatePanel && hasPermission('create-stock-transfer')" @click="showCreatePanel = !showCreatePanel" v-tooltip="trans('general.add_new')"><i class="fas fa-plus"></i> <span class="d-none d-sm-inline">{{trans('inventory.add_new_stock_transfer')}}</span></button>
                        <button class="btn btn-info btn-sm" v-if="!showFilterPanel" @click="showFilterPanel = !showFilterPanel"><i class="fas fa-filter"></i> <span class="d-none d-sm-inline">{{trans('general.filter')}}</span></button>
                        <sort-by :order-by-options="orderByOptions" :sort-by="filter.sort_by" :order="filter.order" @updateSortBy="value => {filter.sort_by = value}"  @updateOrder="value => {filter.order = value}"></sort-by>
                        <div class="btn-group">
                            <button type="button" class="btn btn-info btn-sm dropdown-toggle no-caret " role="menu" id="moreOption" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" v-tooltip="trans('general.more_option')">
                                <i class="fas fa-ellipsis-h"></i> <span class="d-none d-sm-inline"></span>
                            </button>
                            <div :class="['dropdown-menu',getConfig('direction') == 'ltr' ? 'dropdown-menu-right' : '']" aria-labelledby="moreOption">
                                <button class="dropdown-item custom-dropdown" @click="print"><i class="fas fa-print"></i> {{trans('general.print')}}</button>
                                <button class="dropdown-item custom-dropdown" @click="pdf"><i class="fas fa-file-pdf"></i> {{trans('general.generate_pdf')}}</button>
                            </div>
                        </div>
                        <help-button @clicked="help_topic = 'inventory.stock.transfer'"></help-button>
                    </div>
                </div>
            </div>
        </div>
        <div class="container-fluid">
            <transition name="fade">
                <div class="card card-form" v-if="showFilterPanel">
                    <div class="card-body">
                        <h4 class="card-title">{{trans('general.filter')}}</h4>
                        <div class="row">
                            <div class="col-12 col-sm-3">
                                <div class="form-group">
                                    <label for="">{{trans('asset.room')}}</label>
                                    <v-select label="name" track-by="id" v-model="selected_rooms" name="room_id" id="room_id" :options="rooms" :placeholder="trans('inventory.select_room')" @select="onRoomSelect" :multiple="true" :close-on-select="false" :clear-on-select="false" :hide-selected="true" @remove="onRoomRemove" :selected="selected_rooms">
                                        <div class="multiselect__option" slot="afterList" v-if="!rooms.length">
                                            {{trans('general.no_option_found')}}
                                        </div>
                                    </v-select>
                                </div>
                            </div>
                            <div class="col-12 col-sm-6">
                                <date-range-picker :start-date.sync="filter.date_start_date" :end-date.sync="filter.date_end_date" :label="trans('general.date_between')"></date-range-picker>
                            </div>
                        </div>
                        <div class="card-footer text-right">
                            <button type="button" @click="showFilterPanel = false" class="btn btn-danger">{{trans('general.cancel')}}</button>
                            <button type="button" class="btn btn-info waves-effect waves-light" @click="getStockTransfers">{{trans('general.filter')}}</button>
                        </div>
                    </div>
                </div>
            </transition>
            <transition name="fade" v-if="hasPermission('create-stock-transfer')">
                <div class="card card-form" v-if="showCreatePanel">
                    <div class="card-body">
                        <h4 class="card-title">{{trans('inventory.add_new_stock_transfer')}}</h4>
                        <stock-transfer-form @completed="getStockTransfers" @cancel="showCreatePanel = !showCreatePanel"></stock-transfer-form>
                    </div>
                </div>
            </transition>
            <div class="card">
                <div class="card-body">
                    <div class="table-responsive" v-if="stock_transfers.total">
                        <table class="table table-sm">
                            <thead>
                                <tr>
                                    <th>{{trans('inventory.stock_transfer_detail')}}</th>
                                    <th>{{trans('inventory.stock_transfer_date')}}</th>
                                    <th>{{trans('inventory.stock_transfer_return_due_date')}}</th>
                                    <th>{{trans('inventory.stock_transfer_description')}}</th>
                                    <th class="table-option">{{trans('general.action')}}</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="stock_transfer in stock_transfers.data">
                                    <td>
                                        <template v-if="stock_transfer.type == 'employee'">
                                            {{trans('employee.employee_name')+': '+getEmployeeName(stock_transfer.employee)}} <br />
                                            {{getEmployeeDesignationOnDate(stock_transfer.employee, stock_transfer.date)}}
                                        </template>
                                        <template v-else-if="stock_transfer.type == 'student'">
                                            {{trans('student.student_name')+': '+getStudentName(stock_transfer.student)}} <br />
                                            {{trans('student.first_guardian_name')+': '+stock_transfer.student.parent.first_guardian_name}} <br />
                                            {{trans('student.contact_number')+': '+stock_transfer.student.contact_number}} <br />
                                        </template>
                                        <template v-else-if="stock_transfer.type == 'room'">
                                            {{trans('asset.room')}}: {{stock_transfer.room.name}}
                                        </template>
                                    </td>
                                    </td>
                                    <td>{{stock_transfer.date | moment}}</td>
                                    <td>
                                        <template v-if="stock_transfer.return_due_date">
                                            {{stock_transfer.return_due_date | moment}}
                                        </template>
                                        <template v-else>-</template>
                                    </td>
                                    <td v-text="stock_transfer.description"></td>
                                    <td class="table-option">
                                        <div class="btn-group">
                                            <button class="btn btn-success btn-sm" v-tooltip="trans('inventory.stock_transfer_detail')" @click="$router.push('/inventory/stock/transfer/'+stock_transfer.id)"><i class="fas fa-arrow-circle-right"></i></button>
                                            <button class="btn btn-info btn-sm" v-if="hasPermission('edit-stock-transfer')" v-tooltip="trans('inventory.edit_stock_transfer')" @click.prevent="editStockTransfer(stock_transfer)"><i class="fas fa-edit"></i></button>
                                            <button class="btn btn-danger btn-sm" v-if="hasPermission('delete-stock-transfer')" :key="stock_transfer.id" v-confirm="{ok: confirmDelete(stock_transfer)}" v-tooltip="trans('inventory.delete_stock_transfer')"><i class="fas fa-trash"></i></button>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <module-info v-if="!stock_transfers.total" module="inventory" title="stock_transfer_module_title" description="stock_transfer_module_description" icon="list">
                        <div slot="btn">
                            <button class="btn btn-info btn-md" v-if="!showCreatePanel && hasPermission('create-stock-transfer')" @click="showCreatePanel = !showCreatePanel"><i class="fas fa-plus"></i> {{trans('general.add_new')}}</button>
                        </div>
                    </module-info>
                    <pagination-record :page-length.sync="filter.page_length" :records="stock_transfers" @updateRecords="getStockTransfers"></pagination-record>
                </div>
            </div>
        </div>
        <right-panel :topic="help_topic"></right-panel>
    </div>
</template>

<script>
    import stockTransferForm from './form'

    export default {
        components : { stockTransferForm },
        data() {
            return {
                stock_transfers: {
                    total: 0,
                    data: []
                },
                filter: {
                    sort_by : 'date',
                    order: 'desc',
                    room_id: [],
                    date_start_date: '',
                    date_end_date: '',
                    page_length: helper.getConfig('page_length')
                },
                orderByOptions: [
                    {
                        value: 'date',
                        translation: i18n.inventory.stock_transfer_date
                    },
                    {
                        value: 'created_at',
                        translation: i18n.general.created_at
                    }
                ],
                showFilterPanel: false,
                showCreatePanel: false,
                rooms: [],
                selected_rooms: null,
                help_topic: ''
            };
        },
        mounted(){
            if(!helper.hasPermission('list-stock-transfer')){
                helper.notAccessibleMsg();
                this.$router.push('/dashboard');
            }

            this.getStockTransfers();
            helper.showDemoNotification(['inventory']);
        },
        methods: {
            hasPermission(permission){
                return helper.hasPermission(permission);
            },
            getStudentName(student){
                return helper.getStudentName(student);
            },
            getEmployeeName(employee){
                return helper.getEmployeeName(employee);
            },
            getEmployeeDesignationOnDate(employee, date){
                return helper.getEmployeeDesignationOnDate(employee, date);
            },
            getStockTransfers(page){
                let loader = this.$loading.show();
                if (typeof page !== 'number') {
                    page = 1;
                }
                this.filter.date_start_date = helper.toDate(this.filter.date_start_date);
                this.filter.date_end_date = helper.toDate(this.filter.date_end_date);
                let url = helper.getFilterURL(this.filter);
                axios.get('/api/stock/transfer?page=' + page + url)
                    .then(response => {
                        this.stock_transfers = response.stock_transfers;
                        this.rooms = response.filters.rooms;
                        loader.hide();
                    })
                    .catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            editStockTransfer(stock_transfer){
                this.$router.push('/inventory/stock/transfer/'+stock_transfer.id+'/edit');
            },
            confirmDelete(stock_transfer){
                return dialog => this.deleteStockTransfer(stock_transfer);
            },
            deleteStockTransfer(stock_transfer){
                let loader = this.$loading.show();
                axios.delete('/api/stock/transfer/'+stock_transfer.id)
                    .then(response => {
                        toastr.success(response.message);
                        this.getStockTransfers();
                        loader.hide();
                    }).catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            getConfig(config) {
                return helper.getConfig(config)
            },
            print(){
                let loader = this.$loading.show();
                axios.post('/api/stock/transfer/print',{filter: this.filter})
                    .then(response => {
                        let print = window.open("/print");
                        loader.hide();
                        print.document.write(response);
                    })
                    .catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            pdf(){
                let loader = this.$loading.show();
                axios.post('/api/stock/transfer/pdf',{filter: this.filter})
                    .then(response => {
                        loader.hide();
                        window.open('/download/report/'+response+'?token='+this.authToken);
                    })
                    .catch(error => {
                        loader.hide();
                        helper.showErrorMsg(error);
                    });
            },
            onRoomSelect(selectedOption){
                this.filter.room_id.push(selectedOption.id);
            },
            onRoomRemove(removedOption){
                this.filter.room_id.splice(this.filter.room_id.indexOf(removedOption.id), 1);
            },
            formatCurrency(amount) {
                return helper.formatCurrency(amount);
            }
        },
        filters: {
          moment(date) {
            return helper.formatDate(date);
          },
          momentDateTime(date) {
            return helper.formatDateTime(date);
          },
          momentTime(time) {
            return helper.formatTime(time);
          }
        },
        watch: {
            'filter.sort_by': function(val){
                this.getStockTransfers();
            },
            'filter.order': function(val){
                this.getStockTransfers();
            },
            'filter.page_length': function(val){
                this.getStockTransfers();
            }
        },
        computed: {
            authToken(){
                return helper.getAuthToken();
            }
        }
    }
</script>